class Slider extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            length: images.length,
            activePicture: 0,
            sliderHeight: 0
        }
    }
     
    tick() {
        this.setState((prevState) => {
           return { activePicture: (prevState.activePicture + 1) % prevState.length }
        });
    }
    
    componentDidMount() {
        const ratio = 800 / 1920;
        this.setState({ sliderHeight: window.innerWidth * ratio });
        
        this.tickId = setInterval(
            () => this.tick(), 
            3500
        );
    }
    
    componentWillUnmount() {
        clearInterval(this.tickId);
    }
    
    render() {
        return (
            <div className="Slider" style={ {height: this.state.sliderHeight || 0} }>
                { 
                    this.props.pictures.map(
                        (picture, index) => <BigPicture key={ index } active={ this.state.activePicture === index } src={ picture.src } alt={ picture.alt } />
                    )
                }
            </div>
        );
    }
};

