function Input(props) {
    let input, isValid = null;
    
    if (props.type === "textarea") {
        input = <textarea name={ props.name } value={ props.value } onChange={ props.onChange }></textarea>;
    } else {
        input = <input name={ props.name } type={ props.type } value={ props.value } onChange={ props.onChange }/>;
    };
    
    const validity = props.validity;
  
    if (validity != null) {
        if (validity === true) {
            isValid = <span className="validInput"></span>;
        } else if(validity === false) {
            isValid = <span className="invalidInput"></span>;
        }
    }
    
    return (
        <label htmlFor={ props.name }>
            { input }
            { isValid }
            <span className={ props.value ? "formInputHint off" : "formInputHint" }>{ props.hint }</span>
        </label>
    );
}

