let lastScrollY = 0;
let goingDown = false;

class Header extends React.Component {
    constructor(props) {
        super(props);
        
        this.toggleMobileMenu = this.toggleMobileMenu.bind(this);
        this.state = {
            scrollingDown: false,
            mobileMenuOpen: false
        }
    }
    
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }
    
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }
    
    toggleMobileMenu() {
        this.setState((previousState) => {
            return { mobileMenuOpen: !previousState.mobileMenuOpen };
        });
    }
    
    Logo = React.createRef();
    
    handleScroll = () => {
        goingDown = lastScrollY && lastScrollY < window.scrollY;      
        lastScrollY = window.scrollY;
        

        if (goingDown && window.scrollY > 50) {
            this.setState({ scrollingDown: true });
        } else {
            this.setState({ scrollingDown: false });
        }
    }

    render() {
        return (
            <header>
                <nav className={ this.state.scrollingDown ? "navigation top" : "navigation" }>
                    <div className={ this.state.scrollingDown ? 'logoWrapper scaleLogo' : 'logoWrapper' } ref={ this.Logo }>
                        <NavLink to="/">
                            <img className="logo" src={ getPictures.getLogo() } alt="Logo" />
                        </NavLink>
                    </div>
                    <ul className={ "navigationLinks" + (this.state.mobileMenuOpen ? " active" : "")}>
                        {
                            _pages.map(
                                (page, index) => <li key={ 'NavLink' + index }><NavLink onClick={ this.toggleMobileMenu } to={ page.route } activeClassName="active">{ page.name }</NavLink></li>
                            )
                        }
                    </ul>
                    <Hamburger isActive={ this.state.mobileMenuOpen } onClick={ this.toggleMobileMenu } />
                </nav> 
            </header>
        );
    }
};

const Hamburger = (props) => {
    return (
        <div className={ (props.isActive ? "hamburger active" : "hamburger") } onClick={ props.onClick }>
            <div></div>
            <div></div>
            <div></div>
        </div>
    );
}

